<template>
    <div class="date-wapper">
      <el-date-picker
        v-model="startVal"
        type="datetime"
        value-format="yyyy-MM-dd HH:mm:ss"
        @change="changeDate1"
        :editable="false"
        placeholder="">
      </el-date-picker>
      <!--<b>-</b>-->
      <el-date-picker
        v-model="endVal"
        value-format="yyyy-MM-dd HH:mm:ss"
        type="datetime"
        :editable="false"
        @change="changeDate2"
        default-time="23:59:59"
        placeholder="">
      </el-date-picker>
      <el-button @click="searchData" type="primary" class="btn"><i class="el-icon-search"></i></el-button>
    </div>
    
</template>

<script>
    import bus from '../common/bus';
    import {dateFormat} from '../../../static/js/common'
    export default {
        name: "baseDate",
        props:{
            type:{
              type:String
            },
            busName:{
                type:String
            },
            defaultVal:{
                type:Array
            },
        },
        data() {
            return {
              startVal:'',
              endVal:'',
              dateArr:[]
            }
        },
        mounted(){
            //this.dateVal = this.defaultVal;
            let newDate = new Date();
            let curDate = newDate.toLocaleDateString();
            let curYear = newDate.getFullYear();
            let curMonth = newDate.getMonth() + 1;
            let curDay = newDate.getDate();
            let curHour = newDate.getHours();
            let curMin = newDate.getMinutes();
            let curSec = newDate.getSeconds();
            if(curMonth < 10){
                curMonth = '0'+curMonth
            }
            if(curDay < 10){
                curDay = '0' + curDay;
            }
            if(curHour < 10){
                curHour = '0' + curHour;
            }
            if(curMin < 10){
                curMin = '0' + curMin;
            }
            if(curSec < 10){
                curSec = '0' + curSec;
            }
            let startTime = curYear+'-'+curMonth+'-01';
            let endTime = curYear+'-'+curMonth+'-'+curDay+' '+curHour+':'+curMin+':'+curSec;
            /*if(this.type ==='daterange'){
                this.dateVal = [startTime,endTime];
            }else if(this.type === 'datetimerange'){
                this.dateVal2 = [startTime+' 00:00:00',endTime];
            }*/
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            this.startVal =dateFormat('yyyy-mm-dd HH:MM:SS',start)
            this.endVal = dateFormat('yyyy-mm-dd HH:MM:SS',end);
            this.dateArr = [this.startVal,this.endVal]
        },
        watch:{
           /* 'startVal'(){
              console.log(this.startVal)
                // bus.$emit(this.busName,this.dateVal);
              this.dateArr[0] = this.startVal;
            },
            'endVal'(){
              console.log(this.endVal)
                // bus.$emit(this.busName,this.dateVal2)
              this.dateArr[1] = this.endVal;
            },*/
            dateArr:{
             /*
              //
              if(this.dateArr[0] !== '' && this.dateArr[1] !== ''){
                console.log(this.dateArr)
              }*/
              handler(newValue, oldValue) {
                if(this.dateArr[0] !== null && this.dateArr[1] !== null ){
                  bus.$emit(this.busName,this.dateArr)
                }
              },
              //immediate: true,
              deep: true
            }

        },
        methods:{
          /*changeDate1(){
            this.dateArr = [this.startVal,this.endVal]
          },
          changeDate2(){
            this.dateArr = [this.startVal,this.endVal]
          },*/
          searchData(){
            this.dateArr = [this.startVal,this.endVal]
          },
        }
    }
</script>

<style scoped>
  span{
    /*color: #409eff;
    text-shadow: 0;
    text-shadow: initial;*/
  }
  .btn{
    background-color: #185bff;
    border-color: #185bff;
    -webkit-border-radius: 0;
    -moz-border-radius: 0;
    border-radius: 0;
  }
  .date-wapper{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 5px;
  }
  .date-wapper b{
    margin: 0 5px;
    font-weight: 100;
  }
  .date-wapper /deep/ .el-input__inner{
    font-size: 10px;
  }
</style>



