<template>
    <div class="select-wapper">
      <div>
        <span class="select-lable"></span>
        <el-select v-model="refreshTimeVal" @change = "change1" placeholder="" size="mini" class="select-list">
          <el-option
            v-for="item in refreshOpt"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div v-if="this.refreshObj.defaultVal == ''">
        <span class="select-lable"></span>
        <el-select v-model="dataTimeVal" @change = "change2" placeholder="" size="mini" class="select-list">
          <el-option
            v-for="item in dataOpt"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
    </div>
    
</template>

<script>
    import bus from '../common/bus';
    export default {
        name: "flowChartsFrom",
        props:{
          refreshBus:'',
          dataBus:'',
          refreshObj:{
            type:Object,
            default(){
              return{
                defaultVal:'',
                opt:[]
              }
            }
          }
        },
        data() {
            return {
                //
                refreshTimeVal:'5000',
                refreshOpt:[
                    {
                      label:'5s',
                      value:'5000'
                    },
                    {
                        label:'10s',
                        value:'10000'
                    },
                    {
                        label:'30s',
                        value:'30000'
                    },
                    {
                        label:'60s',
                        value:'60000'
                    }
                ],
                //
                dataTimeVal:'3600',
                dataOpt:[
                    {
                        label:'1',
                        value:'3600'
                    },
                    {
                        label:'2',
                        value:'7200'
                    },
                    {
                        label:'4',
                        value:'14400'
                    },
                    {
                        label:'8',
                        value:'28800'
                    },
                    {
                        label:'12',
                        value:'43200'
                    },
                    {
                        label:'24',
                        value:'86400'
                    }
                ]
            }
        },
        created(){
          if (this.refreshObj.defaultVal !== ''){
            this.refreshTimeVal = this.refreshObj.defaultVal;
            this.refreshOpt = this.refreshObj.opt;
          }
        },
        methods:{
            /**/
            change1(){
                bus.$emit(this.refreshBus,this.refreshTimeVal)
            },
            /**/
            change2(){
                bus.$emit(this.dataBus,this.dataTimeVal)
            }
        }
    }
</script>

<style scoped>
    .select-wapper{
        display: flex;
      margin-top: 10px;
      justify-content: center;
    }
    .select-wapper>div{
        /*margin-left: 10px;*/
        display: flex;
        align-items: center;
    }
    .select-lable{
        box-sizing: border-box;
        font-weight: 100!important;
        /*font-size: 16px;*/
        display: inline-block;
        font-size: 12px;
        height: 28px;
        background: 0;
        border: 1px solid #409eff;
        line-height: 28px;
        padding: 0 5px;
        color: #fff;
        word-break: keep-all;
       color: #409eff;
    }
    .select-list{
        width: 80px;
    }
     /deep/ .el-input__inner{
        -webkit-border-radius: 0!important;
        -moz-border-radius: 0!important;
        border-radius:0!important;
        position: relative;
        background: 0;
       border: 1px solid #409eff;
       color: #409eff;
    }
</style>



