<template>
    <div class="echart-wapper" ref="mybox">

    </div>
</template>

<script>
    import bus from '../common/bus';
    const echarts = require('echarts');

    export default {
        name: "echarts",
        data(){
          return{
              clickEchartData:{}//
          }
        },
        props: {
            //
            echartType: {
                type: String
            },
            //
            echartData: {
                type: Object
            },
            //
            clickState:{
                type: Boolean
            },
            //busname
            busName:{
                type:Object,
                default:()=>{
                    return {
                        exportName:'',
                        clickName:''
                    }
                }
            }
        },
        created() {
        },
        mounted() {
        },
        watch:{
          //props
          echartData:{
            handler() {
              this.$nextTick( ()=> {
                this.judgeEchartType()
              })
            },
            immediate: true,
            deep: true
          }
        },
        methods:{
            //
            judgeEchartType(){
              switch (this.echartType) {
                case 'bar':
                  this.barEchart();
                  break;
                case 'morebar':
                  this.barEchart();
                  break;
                case 'pie':
                  this.pieEchart();
                  break;
                case 'ring':
                  this.ringEchart();
                  break;
                case 'line':
                  this.lineEchart();
                  break;
                case 'moreline':
                  this.moreLineEchart();
                  break;
                case 'timeline':
                  this.timeLineEchart();
                  break;
                case 'gauge':
                  this.gaugeEchart();
                  break;
                default:
                  break;
              }
            },
            //
            barEchart(){
                // domecharts
                // var myChart = echarts.init(document.getElementById('echart-wapper'));
                let myChart = echarts.init(this.$refs.mybox,null, {renderer: 'svg'});
                // 
                myChart.setOption({
                    title: {
                        text: this.echartData.baseConfig.title,
                        x:'center',
                        textStyle:{
                          fontSize: '16',
                          color:'#e4956d'
                        }
                    },
                    tooltip: {},
                    grid: {
                      left: '15%',   // 
                      right: '15%', // 
                      top: '15%',   // 
                      bottom: '20%', // 
                    },
                    xAxis: {
                        name:this.echartData.baseConfig.xAxisName,
                        nameTextStyle:{
                            color:'#5bc0de'
                        },
                        axisLine:{
                            lineStyle:{
                                color:'#5bc0de'
                            }
                        },
                        axisLabel:{
                            borderColor:'#5bc0de',
                            fontSize:'10',
                            interval:0,
                            rotate:this.echartData.baseConfig.rotate ? this.echartData.baseConfig.rotate : '0'
                        },
                        data: this.echartData.xAxisArr
                    },
                    yAxis: {
                        name:this.echartData.baseConfig.yAxisName,
                        nameTextStyle:{
                            color:'#5bc0de'
                        },
                        splitLine:{
                            lineStyle:{
                              color:'#0d1c55'
                            }
                        },
                        axisLine:{
                            lineStyle:{
                                color:'#5bc0de'
                            }
                        },
                        axisLabel: {
                          margin: 2,
                          formatter: function (value, index) {
                            if (value >= 10000 && value < 10000000) {
                              value = value / 10000 + "";
                            } else if (value >= 10000000) {
                              value = value / 10000000 + "";
                            }
                            return value;
                          }
                        }
                    },
                    series: [{
                        name: this.echartData.baseConfig.hoverText,
                        type: 'bar',
                        barWidth: 20,
                        itemStyle:{
                          emphasis: {
                            barBorderRadius: 7,
                          },
                          normal: {
                            barBorderRadius: 7,
                            color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [{
                              offset: 0,
                              color: 'rgba(59,118,234,0.3)'
                            }, {
                              offset: 1,
                              color: '#36BBF4'
                            }]),
                            label: {
                              show: true,
                              position: 'top',
                              textStyle: { //
                                color: 'white',
                                fontSize: 10
                              },
                              formatter:function(val){
                                let value = val.data;
                                if (value >= 10000 && value < 10000000) {
                                  value = (value / 10000).toFixed(2) + "";
                                } else if (value >= 10000000) {
                                  value = (value / 10000000).toFixed(2) + "";
                                }
                                return value;
                              }
                            }
                          }
                        },
                        data: this.echartData.yAxisArr
                    }]
                });
              myChart.off('click')
              myChart.on('click',(params)=>{
                //
               //this.$emit('callbackFucnc', params);
                bus.$emit(this.busName.clickName,params)
              })
                /*//
                myChart.on('click',(params)=>{
                    //
                    this.$emit('callbackFucnc', params);
                    bus.$emit(this.busName.clickName,params)
                })
                bus.$emit(this.busName.exportName,myChart)*/
                /*window.addEventListener("resize",()=>{
                    myChart.resize();
                });*/
            },
            //
            lineEchart(){
                let myChart = echarts.init(this.$refs.mybox,null, {renderer: 'svg'});
                myChart.setOption({
                    title: {
                        text:this.echartData.baseConfig.title,
                        x:'center',
                        textStyle:{
                            fontSize: '16',
                            color:'#e4956d'
                        }
                    },
                    tooltip: {
                        /*trigger: 'axis',
                        axisPointer: {
                            type: 'cross',
                            label: {
                                backgroundColor: '#6a7985'
                            }
                        } ,
                        formatter: '{b} <br /> {a} : {c} '*/
                    },
                    legend: {
                        data:[],
                        left: 'left'
                    },
                    grid: {
                      left: '15%',   // 
                      right: '15%', // 
                      top: '20%',   // 
                      bottom: '15%', // 
                    },
                    xAxis: {
                        type: 'category',
                        name:this.echartData.baseConfig.xAxisName,
                        nameTextStyle:{
                            color:'#5bc0de'
                        },
                        axisLine:{
                            lineStyle:{
                                color:'#5bc0de'
                            }
                        },
                        boundaryGap: false,
                        data: this.echartData.xAxisArr
                    },
                    yAxis: {
                        name:this.echartData.baseConfig.yAxisName,
                        axisLine:{
                            lineStyle:{
                                color:'#5bc0de'
                            }
                        },
                        splitLine:{
                            // show:false,
                            lineStyle:{
                                color:'#0d1c55'
                            }
                        },
                        nameTextStyle:{
                            color:'#5bc0de'
                        },
                        axisLabel: {
                          margin: 2,
                          formatter: function (value, index) {
                            if (value >= 10000 && value < 10000000) {
                              value = value / 10000 + "";
                            } else if (value >= 10000000) {
                              value = value / 10000000 + "";
                            }
                            return value;
                          }
                        }
                    },
                    series: [{
                        name:this.echartData.baseConfig.hoverText,
                        type:'line',
                        smooth:true,
                        symbol: 'none',
                        areaStyle: {
                          normal: {
                            // 
                            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                              offset: 0,
                              color: '#00EABD'
                            }, {
                              offset: 1,
                              color: 'rgba(116,235,213,0.1)'
                            }])
                          }
                        },
                        itemStyle:{
                            normal:{
                                color:'#0EBF9C'
                            }
                        },
                        data:this.echartData.yAxisArr
                    }]
                });
               /* //
                myChart.on('click',(params)=>{
                    //
                    //this.$emit('callbackFucnc', params);
                    bus.$emit(this.busName.clickName,params)
                })
                bus.$emit(this.busName.exportName,myChart)*/
                /*window.addEventListener("resize",function(){
                    myChart.resize();
                });*/
            },
            moreLineEchart(){
                echarts.init(this.$refs.mybox).dispose();//
                let myChart = echarts.init(this.$refs.mybox);
                let option = {
                    title: {
                        text:this.echartData.baseConfig.title,
                        x:'center',
                        textStyle:{
                            color:'#5bc0de'
                        }
                    },
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {
                            type: 'cross',
                            label: {
                                backgroundColor: '#6a7985'
                            }
                        }
                    },
                    grid: {
                      left: '15%',   // 
                      right: '15%', // 
                      top: '20%',   // 
                      bottom: '20%', // 
                    },
                    legend: {
                      type:'scroll',
                      pageTextStyle:{
                        color:'#eee',
                      },
                      // pageButtonPosition:'start',
                      data:[],
                      width:'80%',
                      // right:"-10%",
                      top:"-6px",
                      textStyle:{
                        color:'#eee',
                      },
                      pageIconColor:'#fff'
                    },
                    xAxis: {
                        name:this.echartData.baseConfig.xAxisName,
                        nameTextStyle:{
                            color:'#5bc0de'
                        },
                        axisLine:{
                            lineStyle:{
                                color:'#5bc0de'
                            }
                        },
                        boundaryGap: false,
                        data: this.echartData.xAxisArr
                    },
                    yAxis: {
                        name:this.echartData.baseConfig.yAxisName,
                        axisLine:{
                            lineStyle:{
                                color:'#5bc0de'
                            }
                        },
                        splitLine:{
                            lineStyle:{
                                color:'#0d1c55'
                            }
                        },
                        nameTextStyle:{
                            color:'#5bc0de'
                        },
                        axisLabel: {
                          margin: 2,
                          formatter: function (value, index) {
                            if (value >= 10000 && value < 10000000) {
                              value = value / 10000 + "";
                            } else if (value >= 10000000) {
                              value = value / 10000000 + "";
                            }
                            return value;
                          }
                        }
                    },
                    series:[]
                };
                for(let i in this.echartData.yAxisArr ){
                  option.legend.data.push(this.echartData.yAxisArr[i].name);
                  option.series.push({
                    name:this.echartData.yAxisArr[i].name,
                    type: 'line',
                    smooth: true, //
                    symbol: 'none',
                    areaStyle: {
                      normal: {
                        // 
                        color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                          offset: 0,
                          color: this.echartData.yAxisArr[i].color
                        }, {
                          offset: 1,
                          color: 'rgba(116,235,213,0.1)'
                        }])

                      }
                    },
                    itemStyle: {
                      normal: {
                        color: this.echartData.yAxisArr[i].color
                      }
                    },
                    data: this.echartData.yAxisArr[i].data
                  })
                }
                myChart.setOption(option);
               /* //
                myChart.on('click',(params)=>{
                    //
                    //this.$emit('callbackFucnc', params);
                    bus.$emit(this.busName.clickName,params)
                })
                bus.$emit(this.busName.exportName,myChart)*/
               /* window.addEventListener("resize",function(){
                    myChart.resize();
                });*/
            },
            //
            timeLineEchart(){
              let myChart = echarts.init(this.$refs.mybox,null, {renderer: 'svg'});
              let option = {
                title: {
                  text:this.echartData.baseConfig.title,
                  x:'center',
                  textStyle:{
                    color:'#5bc0de'
                  }
                },
                grid: {
                  left: '15%',   // 
                  right: '15%', // 
                  top: '20%',   // 
                  bottom: '20%', // 
                },
                tooltip: {
                  trigger: 'axis',
                  axisPointer: {
                    type: 'cross',
                    label: {
                      backgroundColor: '#6a7985'
                    }
                  }
                },
                legend: {
                  data:[],
                  right:"5%",
                  top:"25px",
                  textStyle:{
                    color:'#eee'
                  }
                },
                xAxis: {
                  name:this.echartData.baseConfig.xAxisName,
                  type: 'time',
                  nameTextStyle:{
                    color:'#5bc0de'
                  },
                  axisLine:{
                    lineStyle:{
                      color:'#5bc0de'
                    }
                  },
                  splitLine: {
                    show: false
                  }
                },
                yAxis: {
                  name:this.echartData.baseConfig.yAxisName,
                  type: 'value',
                  boundaryGap: [0, '100%'],
                  nameTextStyle:{
                    color:'#5bc0de'
                  },
                  axisLine:{
                    lineStyle:{
                      color:'#5bc0de'
                    }
                  },
                  splitLine: {
                    show: false
                  },
                  axisLabel: {
                    margin: 2,
                    formatter: function (value, index) {
                      if (value >= 10000 && value < 10000000) {
                        value = value / 10000 + "";
                      } else if (value >= 10000000) {
                        value = value / 10000000 + "";
                      }
                      return value;
                    }
                  }
                },
                series: []
              };
              for(let i in this.echartData.yAxisArr ){
                option.legend.data.push(this.echartData.yAxisArr[i].name);
                option.series.push({
                  name:this.echartData.yAxisArr[i].name,
                  type: 'line',
                  smooth: true, //.
                  showSymbol: false,
                  hoverAnimation: false,
                  areaStyle: {
                    normal: {
                      // 
                      color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                        offset: 0,
                        color: this.echartData.yAxisArr[i].color
                      }, {
                        offset: 1,
                        color: 'rgba(116,235,213,0.1)'
                      }])

                    }
                  },
                  itemStyle: {
                    normal: {
                      color: this.echartData.yAxisArr[i].color
                    }
                  },
                  data: this.echartData.yAxisArr[i].data
                })
              }
              //option.series = this.echartData.yAxisArr ;
              myChart.setOption(option);
              /*window.addEventListener("resize",function(){
                myChart.resize();
              });*/
            },
            //
            pieEchart(){
                //var myChart = echarts.init(document.getElementById('echart-wapper'));
                let myChart = echarts.init(this.$refs.mybox,null, {renderer: 'svg'});
                myChart.setOption({
                    title : {
                        text: this.echartData.baseConfig.title,
                        x:'center',
                        textStyle:{
                          fontSize: '16',
                          color:'#e4956d'
                        }
                    },
                    tooltip : {
                        trigger: 'item',
                        formatter: "{a} <br/>{b} : {c} ({d}%)"
                    },
                    legend: {
                        orient: 'vertical',
                        left: 'left',
                        data: []
                    },
                    // color:["#5bc0de","#1BB2D8","#99D2DD","#88B0BB","#1C7099","#038CC4","#75ABD0","#AFD6DD","#1790CFs"],
                    color:['#0FBF9C','#E49831','#48A2E3','#E95555','#AE59E3','#5E6EE3','#8196BD','#88B0BB'],
                    series : [
                        {
                            name: this.echartData.baseConfig.hoverText,
                            type: 'pie',
                            radius : [15, 50],
                            roseType : 'radius',
                            center: ['50%', '60%'],
                             /*data:[
                                {value:335, name:'alert multi control'},
                                {value:310, name:'alert_multi_verify'},
                                {value:234, name:'cur_yottabyte'},
                                {value:135, name:'share'},
                                {value:1548, name:'cj test'}
                            ],*/
                            data:this.echartData.yAxisArr,
                            itemStyle: {
                                emphasis: {
                                    shadowBlur: 10,
                                    shadowOffsetX: 0,
                                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                                },
                                opacity:0.9
                              /*color: {
                                type: 'radial',
                                x: 0.5,
                                y: 0.5,
                                r: 0.5,
                                colorStops: [{
                                  offset: 0, color: 'red' // 0% 
                                }, {
                                  offset: 1, color: 'blue' // 100% 
                                }],
                                global: false //  false
                              }*/
                            }
                        }
                    ]
                });
               /* //
                myChart.on('click',(params)=>{
                    //
                    //this.$emit('callbackFucnc', params);
                    bus.$emit(this.busName.clickName,params)
                })
                bus.$emit(this.busName.exportName,myChart)*/
                /*window.addEventListener("resize",function(){
                    myChart.resize();
                });*/
            },
            //
            gaugeEchart(){
              let myChart = echarts.init(this.$refs.mybox,null, {renderer: 'svg'});
              myChart.setOption({
                title : {
                  text: this.echartData.baseConfig.title,
                  x:'center',
                  textStyle:{
                    color:'#5bc0de'
                  }
                },
                tooltip : {
                  formatter: "{a} <br/>{b} : {c}%"
                },
                series: [
                  {
                    name: this.echartData.baseConfig.hoverText,
                    type: 'gauge',
                    title:{
                      color:'#5bc0de'
                    },
                    axisLine: {            // 
                      lineStyle: {       // lineStyle
                        color: [[0.09, 'lime'],[0.82, '#1e90ff'],[1, '#ff4500']],
                        width: 2,
                        shadowColor : '#000', //
                        shadowBlur: 0,
                      }
                    },
                    axisLabel: {            // 
                      textStyle: {       // lineStyle
                        color: '#5bc0de',
                        fontSize:9,   //
                        shadowColor : '#5bc0de', //
                      }
                    },
                    pointer:{
                      width:3
                    },
                    splitLine:{
                      length:10
                    },
                    detail: {
                      formatter:'{value}%',
                      fontSize: 10,
                    },
                    //data: [{value: 10, name: ''}]
                    data:this.echartData.yAxisArr[0].data,
                  }
                ]
              })
             /* window.addEventListener("resize",function(){
                myChart.resize();
              });*/
            }

        }
    }
</script>

<style scoped>
.echart-wapper{
    width: 100%;
    height: 100%;
}
</style>



